.section {
  height: 690px;
  background: url('../../img/background/thirdScreen.jpg');
  background-image: -webkit-image-set(url('../../img/background/thirdScreen.webp') 1x, url('../../img/background/thirdScreen.jp2') 1x, url('../../img/background/thirdScreen.jpg') 1x);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  clip-path: polygon(0% 0%, 100% 5%, 100% 100%, 0% 95%);
  transform: translate(0, -5%);
  -ms-transform: translate(0, 0);
  padding-top: 110px;
}

@media(min-width: 1200px) {
  .section {
    height: 886px;
    clip-path: polygon(0% 0%, 100% 10%, 100% 100%, 0% 90%);
    transform: translate(0, -10%);
    -ms-transform: translate(0, 0);
    padding-top: 89px;
    position: relative;
  }
  .navigation {
    position: absolute;
    left: 60px;
    top: 30%;
  }
  .img {
    display: block;
    position: absolute;
    height: 50%;
    width: auto;
    left: 60%;
    bottom: 24.5%;
    z-index: -1;
  }
}
